import React, { useState, useEffect } from 'react';
import { useParams, Link } from 'react-router-dom';
import { useSetRecoilState } from 'recoil';
import './Tour.css'; // Import the Tour.css file for styling
import BackBar from './component/BackBar';
import { toNmState } from './util/recoild'; // Ensure this path is correct

function Tour() {
  const { api } = useParams();
  const [tourData, setTourData] = useState([]);
  const setToNm = useSetRecoilState(toNmState); // Recoil setter for tournament name

  useEffect(() => {
    fetch(`http://${api}:4000/get-tour`) //대회 목록 가져옴
      .then((res) => res.json())
      .then((json) => {
        setTourData(json);
      })
      .catch((error) => {
        console.error('Error fetching data:', error);
        console.log(process.env.REACT_APP_SERVER);
      });
  }, [api]);

  useEffect(() => {
    if (tourData.length === 1) {
      const singleTour = tourData[0];
      setToNm(singleTour.TO_NM); // Set Recoil state with tournament name
      window.location.href = `/${api}/${singleTour.TO_CD}/login`;
    }
    // else if (tourData.length === 0){
    //   window.location.href = `/${api}/default/login`;
    // }
  }, [tourData, api, setToNm]);

  return (
    <div className="tour-container">
      <BackBar url="/" />
      <h1>대회 선택</h1>
      {tourData.length === 0 ? (
        <p>열린 대회가 존재하지 않아 채점이 불가능합니다.</p>
      ) : (
        <table className="tour-table">
          <thead>
            <tr>
              <th>No</th>
              <th>대회명</th>
            </tr>
          </thead>
          <tbody>
            {tourData.map((tour, index) => (
              <tr key={tour.TO_CD}>
                <td>
                  <Link to={`/${api}/${tour.TO_CD}/login`}>
                    {index + 1}
                  </Link>
                </td>
                <td>
                  <Link to={`/${api}/${tour.TO_CD}/login`}>
                    {tour.TO_NM}
                  </Link>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      )}
    </div>
  );
}

export default Tour;
