// Refree.js
import React, { useState } from 'react';
import { useParams } from 'react-router-dom';
import './Login.css';
import { encryptText } from './util/encryptionUtils';
import { useSetRecoilState } from 'recoil';
import {refreeNameState } from './util/recoild';


const Refree = () => {
  const { api, to_cd } = useParams();
  const [phoneNumber, setPhoneNumber] = useState('');
  const setRefreeName = useSetRecoilState(refreeNameState);
  

  // const login = async () => {
  //   try {
  //     const loginResponse = await fetch(`http://${api}:4000/login`, {
  //       method: 'POST',
  //       headers: {
  //         'Content-Type': 'application/json'
  //       },
  //       mode: 'cors'
  //     });
  
  //     const loginData = await loginResponse.text();
  
  //     // 이후 작업 수행
  //   } catch (error) {
  //     console.error('Error during login:', error);
  //   }
  // };
  
  const fetchRefreeData = async () => {
    try {
      const checkJidResponse = await fetch(`http://${api}:4000/check-jid?phoneNumber=${phoneNumber}&to_cd=${to_cd}`, {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
        }
      });
  
      const checkJidData = await checkJidResponse.json();
      if(checkJidData.jidMatch==='duplicate'){
        alert("같은 번호의 심판이 여러명 존재합니다.");
      }
      else if (!checkJidData.jidMatch) {
        alert("해당 경기에 심판으로 등록되어 있지 않습니다");
      } else {
        setRefreeName(checkJidData.refreeNm);
        const nextLink = `/${api}/${to_cd}/${encryptText(phoneNumber)}`;
        window.location.href = nextLink;
      }
  
    } catch (error) {
      console.error('Error fetching referee data or checking JID:', error);
    }
  };
  

  const handleLoginClick = () => {
    if(to_cd === 'default'){
      const nextLink = `/${api}/default/default`;
      window.location.href = nextLink;
    }
    //const isConfirmed = window.confirm(`${phoneNumber} 이 번호가 맞습니까?`);
    const isConfirmed = true;
    if (isConfirmed) {
      fetchRefreeData();
    }
  };

  const handlePhoneNumberChange = (e) => {
    const formattedPhoneNumber = e.target.value.replace(/\D/g, ''); //숫자만 입력 가능
    setPhoneNumber(formattedPhoneNumber);
  };

  return (
    <div className="login-container">
      <h2>심판 로그인</h2>
      <label>
        비밀번호
        <input
          type="tel"
          value={phoneNumber}
          onChange={handlePhoneNumberChange}
          placeholder="(숫자만 입력 가능합니다)"
        />
      </label>
      <button type="button" onClick={handleLoginClick}>
        로그인
      </button>
    </div>
  );
};

export default Refree;
